import React from 'react';
import logo from '../image/logo.png';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      msg: '',
    }
    fetch(
      'https://overseae-api.herokuapp.com/', 
      {
        method: 'get'
      })
    .then(res => res.text())
    .then(msg => {
      this.setState({
        msg: msg
      });
    })
    .catch(err => console.log(err));
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>{this.state.msg}</p>
        </header>
      </div>
    );
  }
}

export default App;
